import React from 'react';
// import './App.css';
import './assets/css/style.css';//src\assets\css\style.css


function Header() {
    return (       
        <header className="header">
            <img src="https://code.s3.yandex.net/web-code/entrance-test/github-logo.svg" alt = "" className="header__logo" />
            <h1 className="header__title">GitHub Поиск</h1>
        </header>
    )
}

function SearchResult(prop) {
    return (
        <div className="search__result">
            <SearchResultList value = {prop.value}/>
        </div>
    )
}
class SearchResultList extends React.Component {
    constructor(props) {
        super(props);
        // this.value = this.props.value;
        this.state = {
          error: null,
          isLoaded: false,
          items: [],
          count: 0
        };
    }

    componentDidMount() {
        let url = "https://api.nomoreparties.co/github-search?q=" + this.props.value;  
        fetch(url)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result.items,
                        count: result.total_count
                    });
                    console.log("result - ", result); 
                },
            // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
            // чтобы не перехватывать исключения из ошибок в самих компонентах.
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }

    componentDidUpdate(prevProps) {

        let url = "https://api.nomoreparties.co/github-search?q=" + this.props.value;  
        if (this.props.value !== prevProps.value) {

            fetch(url)
                .then(res => res.json())
                .then(
                    (result) => {
                        this.setState({
                            isLoaded: true,
                            items: result.items,
                            count: result.total_count
                        });
                    },
                // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
                // чтобы не перехватывать исключения из ошибок в самих компонентах.
                    (error) => {
                        this.setState({
                            isLoaded: true,
                            error
                        });
                    }
                )
        }
    }

    render() {

        const { error, isLoaded, items, count } = this.state;
        if (error) {
            return (
                <>
                    <img src="https://code.s3.yandex.net/web-code/entrance-test/search.svg" alt="" className="search__error-icon" />
                    <p className="search__error-message">
                        Произошла ошибка...
                    </p>
                </>
            )
        } else if (!isLoaded) {
            return <div>Загрузка...</div>;
        } else {
            return (
                <> 
                    {console.log("total_count - ", count)}
                    {count > 0 &&
                        <>
                            <h2 className="search__findings">
                                Найдено <span className="search__findings-amount">{count.toLocaleString('ru-RU')}</span> результатов
                            </h2>
                            <ul className="search__findings-list">
                                {items.map(item => (
                                    <li className = "search__finding-item" key={item.id}>
                                        {/* <p className="search__finding-name"> */}
                                        <a className="search__finding-name" href={item.html_url} target="_blank" rel="noreferrer">{item.full_name}</a> ;
                                        <span className="search__finding-description">{item.description}</span>
                                    </li>
                                ))}
                            </ul>
                        </>
                    }
                    {count === 0 &&
                        <>
                            <img src="https://code.s3.yandex.net/web-code/entrance-test/search.svg" alt="" className="search__error-icon" />
                            <p className="search__error-message">
                                По вашему запросу ничего не найдено, попробуйте уточнить запрос
                            </p>
                        </>
                    }
                </>
            );
        }
    }
}

export default class App extends React.Component {

    constructor(props){
        super(props)
        this.state = {val: ''};
    }

    handleSubmit = (e) => {
        if(e) e.preventDefault();
        const name = this.input.value;
        this.setState({val: name});
    }

    render() {
       return (
           <>
                <TaskText/>

                <Header/>
                <section className="search">
                    <form className="search__form" onSubmit={this.handleSubmit}>
                        <input className="search__textfield"  placeholder="Your name" type="text" ref={(element_) => {this.input = element_ }} />
                        <button className="search__button">Найти!</button>
                    </form>
                    {/* {this.state.val !== none &&
                            <SearchResult value = {this.state.val}/>
                    } */}
                    {Object.keys(this.state.val).length  !== 0 &&
                            <SearchResult value = {this.state.val}/>
                    }
                </section>
           </>

        )
    }
}


